import React from  "react"

import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import Header from "./header"
import Footer from "./footer"
import 'bootstrap/dist/css/bootstrap.min.css'

const Layout = ({ children }) => {
    const data = useStaticQuery(graphql`
        query SiteTitleQuery {
            site {
                siteMetadata {
                    title
                    author
                }
            }
        }
    `)

    return (
        <>
            <header class="text-light justify-content-center navbar bg-dark">
            <p className="text-center mt-4 mb-4 1h-sm"> 2021, Built with gatsby, algolia, react-bootstrap, github</p>
            </header>
            <Header siteTitle = {data.site.siteMetadata.title} />
            <div 
                style={
                    {
                        margin: `3.0rem 0 0 0`,                        
                        padding: `0 1.0875rem 1.45rem`,
                    }
                }
            >
                <main>{children}</main>                
            </div>
            <Footer siteOuthor = {data.site.siteMetadata.author} />
        </>
    )
}

Layout.propTypes = {
    children: PropTypes.node.isRequired,
}

export default Layout